<template>
    <div class="slider" ref="slider">
        <div class="slider-group" ref="sliderGroup">
            <slot></slot>
        </div>
        <div class="dots">
            <span class="dot" v-for="(dot,index) in dots" :key="index" :class="{'active': index === currentPageIndex}"></span>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll';

import {addClass} from 'common/js/dom';

export default {
    name: 'Slider',
    props: {
        loop: {
            type: Boolean,
            default: true
        },
        autoPlay: {
            type: Boolean,
            default: true
        },
        interval: {
            type: Number,
            default: 4000
        }
    },
    data () {
        return {
            dots: [],
            currentPageIndex: 0
        }
    },
    methods: {
        setSliderWidth (isResize) {

            //  slider 
            //  slider    slider 
            //  slider  slider-item class
            //  slider 
            //  slider 
            //  resize 

            this.children = this.$refs.sliderGroup.children;

            var width = 0;
            var sliderWidth = this.$refs.slider.clientWidth;

            for (let i=0; i<this.children.length; i++) {
                let child = this.children[i];
                addClass(child, 'slider-item');

                child.style.width = sliderWidth + 'px';
                width += sliderWidth ;

            }
            
            // 
            if (this.loop && !isResize) {
                width += 2 * sliderWidth;
            }

            this.$refs.sliderGroup.style.width = width + 'px';
        },
        initSlider () {

            //  BS slider
            this.slider = new BScroll(this.$refs.slider,{
                scrollX: true,
                scrollY: false,
                momentum: false,
                click: true,
                snap: {
                    loop: this.loop,
                    threshold: .3,
                    speed: 400
                }
            })

            //  scrollEnd
            //  slider Index
            // 

            this.slider.on('scrollEnd', () => {

                var pageIndex = this.slider.getCurrentPage().pageX;
                this.currentPageIndex = pageIndex;
            })

            if (this.autoPlay) {
                
                clearTimeout(this.timer);
                this.play();
            }


        },
        initDots () {
            //  slider 
            this.dots = new Array(this.children.length);
        },
        play () {
             //  slider 
            var pageIndex = this.currentPageIndex + 1;

            this.timer = setTimeout(() => {
                this.slider.goToPage(pageIndex,0,400);
            },this.interval)

        }
    },
    mounted() {
        setTimeout(() => {
            this.setSliderWidth();
            this.initDots();
            this.initSlider();

            //  slider
            if (this.autoPlay) {
                this.play();
            }

        },20);

        //  slider 
        window.addEventListener('resize', () => {
            if (!this.slider) return;
            this.setSliderWidth(true);
            this.slider.refresh();
        })   
    },
    destroyed () {
        clearTimeout(this.timer);
    }
}
</script>

<style lang="scss" scoped>
@import '~common/scss/const';

    .slider {
        min-height: 1px;
        position: relative;
        &-group {
            overflow: hidden;
            white-space: nowrap;
        }
        &-item {
            float: left;
            box-sizing: border-box;
            text-align: center;
            overflow: hidden;
            a {
                display: block;
                width: 100%;
                overflow: hidden;
                text-decoration: none;
            }
            img {
                display: block;
                width: 100%;
            }
        }

        .dots {
            position: absolute;
            right: 0;
            left: 0;
            bottom: 12px;
            text-align: center;

            .dot {
                display: inline-block;
                width: 8px;
                height: 8px;
                margin: 0 4px;
                border-radius: 50%;
                background: $color-text-l;

                &.active {
                    width: 20px;
                    border-radius: 5px;
                    background: $color-text-ll;
                }
            }
        }

    }
</style>