<template>
    <scroll class="listview" :data="data" ref="listView" :listenScroll="listenScroll" @scroll="scroll" :probeType="probeType">
        <ul>
            <li v-for="(group,index) in data" :key="index" ref="listGroup">
                <h2 class="list-group-title">{{group.title}}</h2>
                <ul>
                    <li v-for="(item,index) in group.items" @click="selectItem(item)" class="list-group-item" :key="index">
                        <img class="avatar" v-lazy="item.avatar" />
                        <span class="name">{{item.name}}</span>
                    </li>
                </ul>
            </li>
        </ul>
        
        <div class="list-shortcut" @touchstart="onShortcutTouchStart" @touchmove.stop.prevent="onShortcutTouchMove">
            <ul>
                <li v-for="(item,index) in shortcutList" 
                    :key="index"
                    class="item"
                    :class="{'current': currentIndex === index}"
                    :data-index="index"
                >
                    {{item}}
                </li>
            </ul>
        </div>

        <div class="list-fixed" v-show="fixedTitle" ref="fixed">
            <h1 class="fixed-title">{{fixedTitle}}</h1>
        </div>
    </scroll>
</template>

<script>
import Scroll from 'base/scroll/scroll';

import {getData} from 'common/js/dom';

const ANCHOR_HEIGHT = 18;
const TITLE_HEIGHT = 30;

export default {
    components: {
        Scroll
    },
    props: {
        data: {
            type: Array,
            default: function () {
                return []
            }
        }
    },
    data () {
        return {
            scrollY: -1,
            currentIndex: 0,
            diff: -1
        }
    },
    methods: {
        onShortcutTouchStart(e) {
            //touchEvent => touchstart 

            // 
            // 
            // y
            // 

            const anchorIndex = getData(e.target, 'index');
            const firstTouch  = e.touches[0];
            this.touch.y1 = firstTouch.pageY;
            this.touch.anchorIndex = anchorIndex;

            this.scrollTo(anchorIndex)
        },
        onShortcutTouchMove(e) {

            // 
        
            const firstTouch = e.touches[0];
            this.touch.y2  = firstTouch.pageY;            

            //  => 
            const delta = (this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT | 0 ;

            // 
            const anchorIndex = parseInt(this.touch.anchorIndex) + delta;

            this.scrollTo(anchorIndex);

        },
        refresh () {
            this.$refs.listView.refresh();
        },
        scroll (pos) {
            this.scrollY = pos.y
        },
        scrollTo (index) {

            if (!index) return;

            if (index < 0) {
                index = 0;
            }

            if (index > this.listHeight.length - 2) {
                index = this.listHeight.length - 2;
            }

            // 
            // 
            // this.scrollY = -this.listHeight[index]
            this.$refs.listView.scrollToElement(this.$refs.listGroup[index],0);

        },
        calculateHeight () {
            // 
            this.listHeight = [];
            const list = this.$refs.listGroup;

            var height = 0;

            this.listHeight.push(height);

            list.forEach(item => {
                height += item.clientHeight;
                this.listHeight.push(height);
            })            
        },
        selectItem (item) {
            this.$emit('select', item);
        }
    },
    computed: {
        shortcutList () {
            // 
            return this.data.map((item) => item.title)
        },
        fixedTitle () {
            // 

            if (this.scrollY > 0) return;

            return this.data[this.currentIndex] ? this.data[this.currentIndex].title : '';
        }
    },
    watch: {
        data () {
            setTimeout(() => {
                this.calculateHeight();
            },20)
        },
        scrollY(newY) {
            
            // y
            // 
            const listHeight = this.listHeight;

            if (newY > 0) {
                this.currentIndex = 0;
                return;
            }

            for (let i=0; i<listHeight.length - 1; i++) {
                const minHeight = listHeight[i]
                const maxHeight = listHeight[i+1]
                
                if (!maxHeight || (-newY >= minHeight && -newY < maxHeight)) {

                    this.currentIndex = i;

                    //  li   scroll 
                    this.diff = maxHeight + newY;
                    return;
                }
            }
            
            this.currentIndex = listHeight.length - 2;
        },
        diff (delta) {
            var fixedTop = (delta > 0 && delta < TITLE_HEIGHT) ? delta - TITLE_HEIGHT : 0;

            if (this.fixedTop === fixedTop) return;

            this.fixedTop = fixedTop;

            // this.$refs.fixed.style.transofrm = `translateY(${fixedTop}px)`;
            this.$refs.fixed.style.transform = `translate3d(0, ${fixedTop}px, 0)`;
        }
    },
    created () {
        this.touch = {}
        this.listenScroll = true;
        this.listHeight = [];

        //  better-scroll 
        this.probeType = 3;
    }
}
</script>

<style lang="scss" scoped>

@import "~@/common/scss/const.scss";
@import "~@/common/scss/mixin.scss";
.listview {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: $color-background;
    .list-group {
        padding-bottom: 30px;
        &-title {
            padding-left: 20px;
            height: 30px;
            line-height: 30px;
            font-size: $font-size-small;
            color: $color-text-l;
            background: $color-highlight-background;
        }
        &-item {
            display: flex;
            align-items: center;
            padding: 20px 0 0 30px;
            .avatar {
                width: 50px;
                height: 50px;
                border-radius: 50%;
            }
            .name {
                margin-left: 20px;
                color: $color-text-l;
                font-size: $font-size-medium;
            }
        }
    }
    .list-shortcut {
        position: absolute;
        top: 50%;
        right: 0;
        z-index: 30;
        padding: 20px 0;
        border-radius: 10px;
        text-align: center;
        background: $color-background-d;
        font-family: Helvetica;
        transform: translateY(-50%);
        .item {
            padding: 3px;
            line-height: 1;
            color: $color-text-l;
            font-size: $font-size-small;
            &.current {
                color: $color-theme;
            }
        }
    }
    .list-fixed {
        position: absolute;
        top: -1px;
        left: 0;
        width: 100%;
        .fixed-title {
            height: 30px;
            line-height: 30px;
            padding-left: 20px;
            font-size: $font-size-small;
            color: $color-text-l;
            background: $color-highlight-background;
        }
    }
}
</style>